.topnav {
    overflow: hidden;
    background: rgb(31, 27, 99);
    background: linear-gradient(90deg, rgba(31, 27, 99, 1) 9%, rgba(19, 19, 106, 1) 100%, rgba(0, 212, 255, 1) 100%);
  }
  
  .topnav a {
    float: left;
    display: block;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
  }
  
  .topnav a:hover {
    background-color: salmon;
    border-bottom: 2px solid salmon;
    transition: transform .4s linear;
  }
  
  .topnav a.active {
    
    color: white;
  }
  
  .topnav .icon {
    display: none;
  }
  
  @media screen and (max-width: 600px) {
    .topnav a:not(:first-child) {display: none;}
    .topnav a.icon {
      float: right;
      display: block;
    }
  }
  
  @media screen and (max-width: 600px) {
    .topnav.responsive {position: relative;}
    .topnav.responsive .icon {
      position: absolute;
      right: 0;
      top: 0;
      color: white;
    }
    .topnav.responsive a {
      float: none;
      display: block;
      text-align: left;
    }
  }
